import {createContext, useContext, useReducer, ReactNode, useState, useEffect} from 'react';
import {validateTokenOnServer} from "./validateToken";

// 
export interface UserReq {
  username: string;
  password: string;
}

// User,TypeScriptuser
// context
export interface User {
  username: string;
  jwt: string;
}

type State = {
  user: undefined | User;
  permissions: undefined | string[];
  loading: boolean;
};

const initialState: State = {
  user: undefined,
  permissions: undefined,
  loading: false,
};

type Action = | {
  type: 'unauthenticated';
} | {
  type: 'authenticate';
} | {
  type: 'authenticated';
  user: User | undefined;
} | {
  type: 'authorize';
} | {
  type: 'authorized';
  permissions: string[];
};

/**
 * @param state 
 * @param action 
 * actionauthenticated
 * authenticated
 * action
 */
function reducer(state: State, action: Action): State {
  switch (action.type) {
    case 'unauthenticated':
      return {...state, loading: false};
    case 'authenticate':
      return {...state, loading: true};
    case 'authenticated':
      return {...state, loading: false, user: action.user};
    case 'authorize':
      return {...state, loading: true};
    case 'authorized':
      return {
        ...state,
        loading: false,
        permissions: action.permissions,
      };
    default:
      return state;
  }
}

type AppContextType = State & {
  dispatch: React.Dispatch<Action>;
};
const AppContext = createContext<AppContextType>({
  ...initialState,
  dispatch: () => {
  },
});

type Props = {
  children: ReactNode;
};

export function AppProvider({children}: Props) {
  // reduceruseReducer
  // dispatchreduceraction
  // dispatchAppContext.Providerapp
  // dispatchuser permission
  const [{user, permissions, loading}, dispatch] = useReducer(reducer, initialState);
  //
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    validateAndSetInitialState().then(r => {});
  }, []);

  // localStoragejwtusername,localStorage
  // AppProviderjwt
  // initialState
  const validateAndSetInitialState = async () => {
    const savedJwt = localStorage.getItem('jwt');
    const savedUsername = localStorage.getItem('username');

    if (savedJwt && savedUsername) {
      const isValid = await validateTokenOnServer(savedJwt);
      if (isValid) {
        console.log("AppProvider get saved user: ", savedUsername + ' ' + savedJwt);
        initialState.user = { username: savedUsername, jwt: savedJwt };
      } else {
        console.log("AppProvider INVALID saved user");
        initialState.user = undefined;
      }
    } else {
      console.log("AppProvider NO saved user");
    }
    setIsInitialized(true);
  };

  /*
  null
  isInitializedfalseAppProvider
  return null;

  validateAndSetInitialState()isInitialized
  AppProviderisInitialized
  isInitializedtrueAppProvider

  ReactisInitialized
  isInitializedfalsetruenull
   */
  if (!isInitialized) {
    return null; // 
  }

  return (
    <AppContext.Provider
      value={{
        user,
        permissions,
        loading,
        dispatch,
      }}
    >
      {children}
    </AppContext.Provider>
  );
}

export const useAppContext = () => useContext(AppContext);
